<template>
  <b-row
    v-if="(currentPage - 1) * perPage <= index && index < currentPage * perPage"
    class="m-2"
    @click="selectHouse"
    @mouseover="colorChange(true)"
    @mouseout="colorChange(false)"
    :class="{ 'mouse-over-bgcolor': isColor, bgcolor: !isColor }"
  >
    <b-col cols="2" class="text-center align-self-center">
      <b-img
        thumbnail
        src="https://picsum.photos/250/250/?image=58"
        alt="Image 1"
      ></b-img>
    </b-col>
    <b-col cols="10" class="align-self-center">
      {{ index + 1 - (currentPage - 1) * perPage }} :
      {{ house.aptName }} [aptCode : {{ house.aptCode }}]
    </b-col>
  </b-row>
</template>

<script>
import { mapActions, mapState } from "vuex";

const houseStore = "houseStore";

export default {
  name: "HouseListItem",
  data() {
    return {
      isColor: false,
    };
  },
  props: {
    house: Object,
    index: Number,
    //perPage: Number,
  },
  computed: {
    ...mapState(houseStore, ["currentPage", "perPage"]),
  },
  methods: {
    ...mapActions(houseStore, ["detailHouse"]),
    selectHouse() {
      // console.log("listRow : ", this.house);
      // this.$store.dispatch("getHouse", this.house);
      this.detailHouse(this.house);
    },
    colorChange(flag) {
      this.isColor = flag;
    },
  },
};
</script>

<style scoped>
.apt {
  width: 50px;
}
.mouse-over-bgcolor {
  background-color: lightblue;
}
.bgcolor {
  background-color: rgb(210, 238, 245);
}
</style>
