<template>
  <b-container v-if="house" class="bv-example-row">
    <b-modal
      id="modal-store"
      size="lg"
      title="주변상권"
      scrollable
      centered
      :header-bg-variant="headerBgVariant"
      :header-text-variant="headerTextVariant"
    >
      <store-modal></store-modal
    ></b-modal>
    <b-row>
      <b-col>
        <b-alert show variant="primary"
          ><h3>{{ house.aptName }}</h3></b-alert
        ></b-col
      >
    </b-row>
    <b-row class="mb-2 mt-1">
      <b-col
        ><b-img :src="require('@/assets/apt.png')" fluid-grow></b-img
      ></b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-alert show variant="secondary"
          >아파트 코드 : {{ house.aptCode }}</b-alert
        >
      </b-col>
      <b-col>
        <b-button variant="primary" size="lg" class="mr-2" v-b-modal.modal-store
          >주변 상권</b-button
        >
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-alert show variant="primary"
          >아파트 이름 : {{ house.aptName }}
        </b-alert>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-alert show variant="info"
          >주소 : {{ house.sidoName }} {{ house.gugunName }}
          {{ house.dongName }}
        </b-alert>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-alert show variant="warning">지번 : {{ house.jibun }}</b-alert>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-alert show variant="danger"
          >최근거래금액 :
          {{
            (parseInt(house.recentPrice.replace(",", "")) * 10000) | price
          }}원</b-alert
        >
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-form>
          <label class="e">이 집을 사려면 얼마나 걸릴까?</label>
          <b-form-input
            id="subject"
            type="text"
            v-model="asset"
            required
            placeholder="현재 자산을 숫자만 입력하세요"
            ref="asset"
          ></b-form-input>
          <br />
          <b-form-input
            id="subject"
            type="text"
            v-model="save"
            required
            placeholder="월 평균 저축액을 숫자만 입력하세요"
            ref="save"
          ></b-form-input>
          <b-button
            type="submit"
            variant="primary"
            class="m-3"
            @click="onSubmit"
            >확인</b-button
          >
        </b-form>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import { mapState } from "vuex";
import StoreModal from "@/components/house/StoreModal.vue";

const houseStore = "houseStore";

export default {
  name: "HouseDetail",
  data() {
    return {
      asset: "",
      save: "",
      year: "",
      month: "",
      price: "",
      assetf: "",
      savef: "",
      headerBgVariant: "dark",
      headerTextVariant: "light",
    };
  },
  components: {
    StoreModal,
  },
  methods: {
    onSubmit(event) {
      event.preventDefault();

      let err = true;
      let msg = "";

      !this.asset &&
        ((msg = "현재 자산을 입력해주세요"),
        (err = false),
        this.$refs.asset.focus());
      err &&
        !this.save &&
        ((msg = "월 평균 저축금액을 입력해주세요"),
        (err = false),
        this.$refs.save.focus());

      if (!err) alert(msg);
      else this.result();
    },
    result() {
      this.price = this.house.recentPrice.replace(",", "") * 10000;
      this.month = parseInt((this.price - this.asset) / this.save);
      this.year = parseInt(this.month / 12);
      this.month = this.month % 12;
      alert(
        "이 집을 사기 위해서는 " +
          this.year +
          "년 " +
          this.month +
          "개월이 걸립니다. \n (이자,집값 상승률 반영X)",
      );
    },
  },
  computed: {
    ...mapState(houseStore, ["house"]),
    // house() {
    //   return this.$store.state.house;
    // },
  },
  filters: {
    price(value) {
      if (!value) return value;
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
  created() {
    this.price = this.house.recentPrice;
  },
};
</script>

<style>
.e {
  font-family: cursive;
}
</style>
