<template>
  <b-container>
    <b-row class="float-right">
      <b-col class="">
        <b-button
          variant="outline-info"
          size="sm"
          @click="displayMarkers"
          class="mr-2"
          >지도 갱신</b-button
        >
      </b-col>
      <b-col class="">
        <b-form-select
          v-model="selected"
          :options="options"
          @change="getPerPage(selected)"
        ></b-form-select>
      </b-col>
    </b-row>
    <b-row></b-row>
    <div id="container">
      <div id="rvWrapper">
        <div id="roadview" style="width: 100%; height: 100%"></div>
        <!-- 로드뷰를 표시할 div 입니다 -->
        <div id="close" title="로드뷰닫기" @click="closeRoadview">
          <span class="img"></span>
        </div>
      </div>
      <div id="mapWrapper">
        <!-- 지도를 표시할 div 입니다 -->
        <div id="map" style="width: 100%; height: 100%"></div>
        <div id="roadviewControl" @click="setRoadviewRoad"></div>
      </div>
    </div>
  </b-container>
</template>

<script>
import { mapState, mapActions } from "vuex";

const houseStore = "houseStore";

export default {
  name: "KakaoMap",
  data() {
    return {
      map: null,
      markers: [],
      infowindow: null,
      customOverlay: null,
      //geocoder: null,
      root: "",
      selected: 5,
      options: [
        { value: 5, text: "페이지당 검색 수(기본 5개)" },
        { value: 10, text: "10개" },
        { value: 20, text: "20개" },
      ],
      overlayOn: false, // 지도 위에 로드뷰 오버레이가 추가된 상태를 가지고 있을 변수
      container: null, // 지도와 로드뷰를 감싸고 있는 div 입니다
      mapWrapper: null, // 지도를 감싸고 있는 div 입니다
      mapContainer: null, // 지도를 표시할 div 입니다
      rvContainer: null, //로드뷰를 표시할 div 입니다
      rv: null,
      rvClient: null,
      markImage: null,
      marker: null,
      mapCenter: null,
    };
  },
  computed: {
    ...mapState(houseStore, ["houses", "currentPage", "perPage"]),
    changeMap() {
      return this.houses[0].aptName;
    },
  },
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");

      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src = `//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=${process.env.VUE_APP_KAKAOMAP_KEY}&libraries=services`; //&libraries=services
      document.head.appendChild(script);
    }

    // kakao.maps.event.addListener(this.rv, "position_changed", () => {
    //   //alert("작동되냐");
    //   // 현재 로드뷰의 위치 좌표를 얻어옵니다
    //   var rvPosition = this.rv.getPosition();

    //   // 지도의 중심을 현재 로드뷰의 위치로 설정합니다
    //   this.map.setCenter(rvPosition);

    //   // 지도 위에 로드뷰 도로 오버레이가 추가된 상태이면
    //   if (this.overlayOn) {
    //     // 마커의 위치를 현재 로드뷰의 위치로 설정합니다
    //     this.marker.setPosition(this.rvPosition);
    //   }
    // });

    // kakao.maps.event.addListener(this.map, "click", (mouseEvent) => {
    //   alert("작동되냐");
    //   // 지도 위에 로드뷰 도로 오버레이가 추가된 상태가 아니면 클릭이벤트를 무시합니다
    //   if (!this.overlayOn) {
    //     return;
    //   }

    //   // 클릭한 위치의 좌표입니다
    //   var position = mouseEvent.latLng;
    //   // 마커를 클릭한 위치로 옮깁니다
    //   this.marker.setPosition(position);

    //   // 클락한 위치를 기준으로 로드뷰를 설정합니다
    //   this.toggleRoadview(position);
    // });
  },

  methods: {
    ...mapActions(houseStore, ["getPerPage"]),
    initMap() {
      var container = document.getElementById("map");
      this.mapCenter = new kakao.maps.LatLng(37.501449, 127.03953);
      var options = {
        center: this.mapCenter,
        level: 1,
      };

      //지도 객체를 등록합니다.
      //지도 객체는 반응형 관리 대상이 아니므로 initMap에서 선언합니다.

      this.map = new kakao.maps.Map(container, options);

      this.infowindow = new kakao.maps.InfoWindow({ zIndex: 1 });
      //this.geocoder = new kakao.maps.services.Geocoder();
      this.container = document.getElementById("container"); // 지도와 로드뷰를 감싸고 있는 div 입니다
      this.mapWrapper = document.getElementById("mapWrapper"); // 지도를 감싸고 있는 div 입니다
      this.mapContainer = document.getElementById("map"); // 지도를 표시할 div 입니다
      this.rvContainer = document.getElementById("roadview"); //로드뷰를 표시할 div 입니다
      this.rv = new kakao.maps.Roadview(this.rvContainer);
      this.rvClient = new kakao.maps.RoadviewClient();
      this.markImage = new kakao.maps.MarkerImage(
        "https://t1.daumcdn.net/localimg/localimages/07/2018/pc/roadview_minimap_wk_2018.png",
        new kakao.maps.Size(26, 46),
        {
          // 스프라이트 이미지를 사용합니다.
          // 스프라이트 이미지 전체의 크기를 지정하고
          spriteSize: new kakao.maps.Size(1666, 168),
          // 사용하고 싶은 영역의 좌상단 좌표를 입력합니다.
          // background-position으로 지정하는 값이며 부호는 반대입니다.
          spriteOrigin: new kakao.maps.Point(705, 114),
          offset: new kakao.maps.Point(13, 46),
        },
      );

      this.marker = new kakao.maps.Marker({
        image: this.markImage,
        position: this.mapCenter,
        draggable: true,
      });

      kakao.maps.event.addListener(this.rv, "position_changed", () => {
        // 현재 로드뷰의 위치 좌표를 얻어옵니다
        //alert(this.rv.getPosition().getLat().toFixed(5));
        //var rvPosition = this.rv.getPosition();
        var rvPosition = new kakao.maps.LatLng(
          this.rv.getPosition().getLat().toFixed(9),
          this.rv.getPosition().getLng().toFixed(9),
        );
        // 지도의 중심을 현재 로드뷰의 위치로 설정합니다
        this.map.setCenter(rvPosition);
        // 지도 위에 로드뷰 도로 오버레이가 추가된 상태이면
        if (this.overlayOn) {
          // 마커의 위치를 현재 로드뷰의 위치로 설정합니다
          //이거 왠지 안되니 일단 블록
          //this.marker.setPosition(this.mapCenter);
          this.marker.setPosition(rvPosition);
        }
      });

      kakao.maps.event.addListener(this.map, "click", (mouseEvent) => {
        // 지도 위에 로드뷰 도로 오버레이가 추가된 상태가 아니면 클릭이벤트를 무시합니다
        if (!this.overlayOn) {
          return;
        }

        // 클릭한 위치의 좌표입니다
        var position = mouseEvent.latLng;
        //alert(position);
        // 마커를 클릭한 위치로 옮깁니다
        this.marker.setPosition(position);
        //this.map.setLevel(3);
        // 클락한 위치를 기준으로 로드뷰를 설정합니다
        this.toggleRoadview(position);
      });
    },
    // 검색 결과 목록과 마커를 표출하는 함수입니다
    displayMarkers() {
      //var fragment = document.createDocumentFragment();
      var bounds = new kakao.maps.LatLngBounds();

      //var listStr = "";
      //console.log(houses.length);
      //지도에 표시되고 있는 마커를 제거합니다
      this.removeMarker();
      var i = 0;
      var markNum = 0;
      this.houses.forEach((house) => {
        //alert(house.lat);
        if (
          i < (this.currentPage - 1) * this.perPage ||
          i >= this.currentPage * this.perPage
        ) {
          i++;
        } else {
          var placePosition = new kakao.maps.LatLng(house.lat, house.lng);
          //var marker = this.addMarker(placePosition, markNum);
          this.addMarker(placePosition, markNum);
          //var itemEl = this.getListItem(markNum, house); // 검색 결과 항목 Element를 생성합니다

          // 검색된 장소 위치를 기준으로 지도 범위를 재설정하기위해
          // LatLngBounds 객체에 좌표를 추가합니다
          bounds.extend(placePosition);
          // 마커와 검색결과 항목에 mouseover 했을때
          // 해당 장소에 인포윈도우에 장소명을 표시합니다
          // mouseout 했을 때는 인포윈도우를 닫습니다

          //마커 이벤트 부분 왠지 안된다 일단 블록
          // ((marker, title, code, place) => {
          //   kakao.maps.event.addListener(marker, "click", () => {
          //     this.displayInfowindow(marker, title, place);
          //   });

          //   kakao.maps.event.addListener(this.map, "click", () => {
          //     console.log(this.customOverlay);
          //     this.customOverlay.setMap(null);
          //   });

          //   itemEl.onmouseover = () => {
          //     this.displayInfowindow(marker, title);
          //   };

          //   itemEl.onmouseout = () => {
          //     this.customOverlay.setMap(null);
          //   };
          // })(marker, house.aptName, house.aptCode, house);

          //fragment.appendChild(itemEl);
          i++;
          markNum++;
        }
      });
      // 마커를 생성하고 지도에 표시합니다

      // 검색된 장소 위치를 기준으로 지도 범위를 재설정합니다
      this.map.setBounds(bounds);
    },
    // 마커를 생성하고 지도 위에 마커를 표시하는 함수입니다
    addMarker(position, idx) {
      var imageSrc =
          "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png", // 마커
        // 이미지
        // url,
        // 스프라이트
        // 이미지를
        // 씁니다
        imageSize = new kakao.maps.Size(36, 37), // 마커 이미지의 크기
        imgOptions = {
          spriteSize: new kakao.maps.Size(36, 691), // 스프라이트 이미지의 크기
          spriteOrigin: new kakao.maps.Point(0, idx * 46 + 10), // 스프라이트 이미지 중
          // 사용할 영역의 좌상단
          // 좌표
          offset: new kakao.maps.Point(13, 37), // 마커 좌표에 일치시킬 이미지 내에서의 좌표
        },
        markerImage = new kakao.maps.MarkerImage(
          imageSrc,
          imageSize,
          imgOptions,
        ),
        marker = new kakao.maps.Marker({
          position: position, // 마커의 위치
          image: markerImage,
        });

      marker.setMap(this.map); // 지도 위에 마커를 표출합니다
      this.markers.push(marker); // 배열에 생성된 마커를 추가합니다

      //return marker;
    },

    // 지도 위에 표시되고 있는 마커를 모두 제거합니다
    removeMarker() {
      for (var i = 0; i < this.markers.length; i++) {
        this.markers[i].setMap(null);
      }
      this.markers = [];
    },

    // 검색결과 목록 또는 마커를 클릭했을 때 호출되는 함수입니다
    // 인포윈도우에 장소명을 표시합니다
    // displayInfowindow(marker, title, place) {
    //   var content = `
    //     <div class="overlaybox">
    //       <div class="boxtitle">${title}</div>
    //       <div class="first"><img src="./assets/apt.png" style="width:247px; height:136px;" alt=""></div>
    //       <ul>
    //         <li class="up">
    //           <span class="title">건축년도</span>
    //           <span class="count">${place.buildYear}</span>
    //         </li>
    //         <li>
    //           <span class="title">주소</span>
    //           <span class="count">${place.sidoName} ${place.gugunName} ${place.dongName} ${place.jibun}</span>
    //         </li>
    //         <li>
    //           <span class="title">최신거래금액</span>
    //           <span class="count">${place.recentPrice}</span>
    //         </li>
    //         <li>
    //           <span class="last" id="recenthistor" data-toggle="modal" data-target="#myModal">아파트정보 update</span>
    //         </li>
    //       </ul>
    //     </div>
    //   `;
    //   var position = new kakao.maps.LatLng(
    //     marker.getPosition().getLat() + 0.00033,
    //     marker.getPosition().getLng() - 0.00003,
    //   );
    //   this.customOverlay = new kakao.maps.CustomOverlay({
    //     position: position,
    //     content: content,
    //     xAnchor: 0.3,
    //     yAnchor: 0.91,
    //   });
    //   this.customOverlay.setMap(this.map);
    // },
    //검색결과 항목을 Element로 반환하는 함수입니다
    getListItem(index, place) {
      var el = document.createElement("li");
      var itemStr = `
        <span class="markerbg marker_${index + 1}></span>
        <div class="info"><h5>${place.aptName}</h5> <button>관심등록</button>
        <span>${place.sidoName} ${place.gugunName} ${place.dongName} ${
        place.jibun
      }</span>
	    `;
      el.innerHTML = itemStr;
      el.className = "item";

      return el;
    },
    // 검색결과 목록의 자식 Element를 제거하는 함수입니다
    removeAllChildNods(el) {
      while (el.hasChildNodes()) {
        el.removeChild(el.lastChild);
      }
    },
    toggleRoadview(position) {
      this.rvClient.getNearestPanoId(position, 100, (panoId) => {
        //alert(panoId);
        // 파노라마 ID가 null 이면 로드뷰를 숨깁니다
        if (panoId === null) {
          this.toggleMapWrapper(true, position);
        } else {
          this.toggleMapWrapper(false, position);

          // panoId로 로드뷰를 설정합니다
          this.rv.setPanoId(panoId, position);
        }
      });
    },

    // 지도를 감싸고 있는 div의 크기를 조정하는 함수입니다
    toggleMapWrapper(active, position) {
      if (active) {
        // 지도를 감싸고 있는 div의 너비가 100%가 되도록 class를 변경합니다
        this.container.className = "";

        // 지도의 크기가 변경되었기 때문에 relayout 함수를 호출합니다
        this.map.relayout();

        // 지도의 너비가 변경될 때 지도중심을 입력받은 위치(position)로 설정합니다
        this.map.setCenter(position);
      } else {
        // 지도만 보여지고 있는 상태이면 지도의 너비가 50%가 되도록 class를 변경하여
        // 로드뷰가 함께 표시되게 합니다
        if (this.container.className.indexOf("view_roadview") === -1) {
          this.container.className = "view_roadview";

          // 지도의 크기가 변경되었기 때문에 relayout 함수를 호출합니다
          this.map.relayout();

          // 지도의 너비가 변경될 때 지도중심을 입력받은 위치(position)로 설정합니다
          this.map.setCenter(position);
        }
      }
    },

    // 지도 위의 로드뷰 도로 오버레이를 추가,제거하는 함수입니다
    toggleOverlay(active) {
      if (active) {
        this.overlayOn = true;

        // 지도 위에 로드뷰 도로 오버레이를 추가합니다
        this.map.addOverlayMapTypeId(kakao.maps.MapTypeId.ROADVIEW);

        // 지도 위에 마커를 표시합니다
        this.marker.setMap(this.map);

        // 마커의 위치를 지도 중심으로 설정합니다
        this.marker.setPosition(this.map.getCenter());

        // 로드뷰의 위치를 지도 중심으로 설정합니다
        //alert(this.map.getCenter());
        this.toggleRoadview(this.map.getCenter());
      } else {
        this.overlayOn = false;

        // 지도 위의 로드뷰 도로 오버레이를 제거합니다
        this.map.removeOverlayMapTypeId(kakao.maps.MapTypeId.ROADVIEW);

        // 지도 위의 마커를 제거합니다
        this.marker.setMap(null);
      }
    },

    // 지도 위의 로드뷰 버튼을 눌렀을 때 호출되는 함수입니다
    setRoadviewRoad() {
      var control = document.getElementById("roadviewControl");

      // 버튼이 눌린 상태가 아니면
      if (control.className.indexOf("active") === -1) {
        control.className = "active";

        // 로드뷰 도로 오버레이가 보이게 합니다
        this.toggleOverlay(true);
      } else {
        control.className = "";

        // 로드뷰 도로 오버레이를 제거합니다
        this.toggleOverlay(false);
      }
    },

    // 로드뷰에서 X버튼을 눌렀을 때 로드뷰를 지도 뒤로 숨기는 함수입니다
    closeRoadview() {
      var position = this.marker.getPosition();
      this.toggleMapWrapper(true, position);
    },
  },
};
</script>

<style scoped>
#map {
  width: 100%;
  height: 500px;
}
#container {
  overflow: hidden;
  width: 100%;
  height: 500px;
  position: relative;
}
#mapWrapper {
  width: 100%;
  height: 500px;
  z-index: 1;
}
#rvWrapper {
  width: 50%;
  height: 500px;
  top: 0;
  right: 0;
  position: absolute;
  z-index: 0;
}
#container.view_roadview #mapWrapper {
  width: 50%;
}
#roadviewControl {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 42px;
  height: 42px;
  z-index: 1;
  cursor: pointer;
  background: url(https://t1.daumcdn.net/localimg/localimages/07/2018/pc/common/img_search.png)
    0 -450px no-repeat;
}
#roadviewControl.active {
  background-position: 0 -350px;
}
#close {
  position: absolute;
  padding: 4px;
  top: 5px;
  left: 5px;
  cursor: pointer;
  background: #fff;
  border-radius: 4px;
  border: 1px solid #c8c8c8;
  box-shadow: 0px 1px #888;
}
#close .img {
  display: block;
  background: url(https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/rv_close.png)
    no-repeat;
  width: 14px;
  height: 14px;
}
</style>
